@charset "UTF-8";
.container {
  width: 100%;
  height: 100%;
}

.weather {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  max-width: 740px;
  height: 580px;
  margin: 1.5rem auto;
  border-radius: 25px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  opacity: 0.9;
  background-repeat: no-repeat;
  background-image: url("https://firebasestorage.googleapis.com/v0/b/mtwme-a1870.appspot.com/o/images%2FbgImg.jpg?alt=media&token=974a74ad-e5f4-4760-8764-05a78994489a");
  background-size: cover;
  position: relative;
}

.weatherSun {
  background-image: url("https://firebasestorage.googleapis.com/v0/b/mtwme-a1870.appspot.com/o/images%2FСолнечная-погода.jpg?alt=media&token=6d7b296b-6968-4b86-a2d3-efe6b9c9ebc6");
}

.weatherCloud {
  background-image: url("https://firebasestorage.googleapis.com/v0/b/mtwme-a1870.appspot.com/o/images%2Fпасмурная_погода.jpg?alt=media&token=d937b36a-c11c-49b7-89f2-6116dd1358d5");
}

.weatherRain {
  background-image: url("https://firebasestorage.googleapis.com/v0/b/mtwme-a1870.appspot.com/o/images%2Frain.jpg?alt=media&token=f975f5cd-6a70-40f9-9bfb-f4bfa06cf333");
}
