.message {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  
  .messageTop{
      display: flex;
  }
  
  .messageImg {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
  }
  
  .messageText{
      padding: 10px;
      border-radius: 20px;
      background-color: rgb(228, 228, 228);
      color: black;
      min-width:50px;
      max-width: 40vw;
      word-break: break-word;
      text-align: left;
  }
  
  .messageBottom{
      font-size: 12px;
      margin-top: 10px;
  }
  
  .message.own{
      align-items: flex-end;
      
  }
  
  .message.own .messageText{
    background-color: #1876f27c;
    color: white;
  }