.arrow {
    width: 44px;
    height: 44px;
    border: none;
    background-color: #fff;
    border-radius: 50%;
    filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.4));
    &:hover {
        filter: drop-shadow(0px 0px 5px rgba(0, 0, 0, 0.7));
    }
    z-index: 3;
}

.arrowRight {
    background-image: url('https://firebasestorage.googleapis.com/v0/b/mtwme-a1870.appspot.com/o/images%2FrightArrow.svg?alt=media&token=568a936a-96bd-4bd8-a69b-8c9a2a7153b8');
    background-repeat: no-repeat;
    background-size: 12px;
    background-position: center;
}

.arrowLeft {
    background-image: url('https://firebasestorage.googleapis.com/v0/b/mtwme-a1870.appspot.com/o/images%2FleftArrow.svg?alt=media&token=b58d9563-f57b-4a44-8140-940c897bde4f');
    background-repeat: no-repeat;
    background-size: 12px;
    background-position: center;
}